<template>
<div class="main-container">
    <div class="login-wrapper">
        <div class="login">
            <div>
                <h3>Sign In</h3>
            </div>
            <div>
                <input type="text" v-model="email" placeholder="email" /><br />
                <input type="password" v-model="password" placeholder="password" /><br />
            </div>
            <div>
                <button style="margin-right: 10px;" @click="login">Login</button>
                <button @click="googleLogin" class="google-button">
                     <font-awesome-icon :icon="['fab', 'google-plus']" /> Login
                </button>
                <p><router-link to="/signup">You dont have an account ? You can create one</router-link></p>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import firebase from "firebase";
import {
    db
} from "../main";
export default {
    name: "login",
    data() {
        return {
            email: "",
            password: "",
        };
    },
    methods: {
        login: function () {
            firebase
                .auth()
                .signInWithEmailAndPassword(this.email, this.password)
                .then(
                    function () {
                        alert("Well done ! You are connected");
                        this.$router.replace("runs");
                    },
                    function (err) {
                        alert("Oops. " + err.message);
                    }
                );
        },
        googleLogin: function () {
            const provider = new firebase.auth.GoogleAuthProvider();

            firebase
                .auth()
                .signInWithPopup(provider)
                .then((user) => {
                    if (user.additionalUserInfo.isNewUser) {
                        const newUser = {
                            uid: user.user.uid,
                            email: user.user.email,
                            photoURL: user.user.photoURL,
                        };
                        db.collection("users")
                            .doc(user.user.uid)
                            .set(newUser)
                            .then()
                            .catch((err) => console.log(err));
                    }
                    this.$router.replace("runs");
                })
                .catch((err) => {
                    alert("OOps" + err.message);
                });
        },
    },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&display=swap");

/* "scoped" attribute limit the CSS to this component only */
.login {
    margin-top: 40px;
}

.google-button {
    color: white;
    background: #d34836;
    border: 1px solid #d34836;
}

p {
    margin-top: 40px;
    font-size: 18x;
    color: #9933ff;
    cursor: pointer;
}

p a {
    text-decoration: underline;
    cursor: pointer;
    color: #9933ff;
}

.login-wrapper {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    font-family: "Lobster Two", cursive;
}

input {
    font-size: 25px;
    margin: 20px;
    border-radius: 5px 5px 5px 5px;
    font-family: "Lobster Two", cursive;
    border: 1px solid #400080;
    text-align: center;
}

button {
    margin-top: 25px;
    border: 1px solid #400080;
    font-size: 25px;
    border-radius: 5px 5px 5px 5px;
    font-family: "Lobster Two", cursive;
    background-color: #400080;
    color: white;
    cursor: pointer;
    padding: 5px;
}

input[placeholder]{
  color: #400080;
  align-content: center;
}

h3 {
    font-size: 35px;
    color: #9933ff;
}
</style>
