<template>
  <div class="main-container">
    <div class="showrun-wrapper">
      <div class="column-left">
        <font-awesome-icon
          @click="deleteRun(run)"
          icon="trash"
          class="icon-trash"
        />
        <div class="inner-wrapper">
          <div class="inner-up">
            <div class="run-name">
              <h1>{{ run.name }}</h1>
            </div>
          </div>
          <div class="inner-down">
            <div class="run-distance">
              <p>
                <font-awesome-icon icon="road" class="icon" />{{ run.distance }}
                km
              </p>
            </div>
            <div class="run-where">
              <p>
                <font-awesome-icon icon="map-marker-alt" class="icon" />{{
                  run.where
                }}
              </p>
            </div>
            <div class="run-date">
              <font-awesome-icon icon="calendar-alt" class="icon" />{{
                run.endDateOfRun
              }}
            </div>
          </div>
        </div>
      </div>
      <div class="column-right">
        <div class="counter">
          <div v-if="date">
            <font-awesome-icon icon="stopwatch" class="icon-stopwatch" />
          </div>
          <div v-if="timeToRun">
            <font-awesome-icon icon="running" class="icon-stopwatch" />
          </div>
          <div v-if="timeToRun" class="counter-item-timeToRun">
            <div class="counter-text">
              {{ timeToRun }}
            </div>
          </div>

          <div v-if="date.years" class="counter-item">
            <div class="counter-item-date">
              {{ date.years }}
            </div>
            <div class="counter-text">
              Year
            </div>
          </div>
          <div v-if="date.months" class="counter-item">
            <div class="counter-item-date">
              {{ date.months }}
            </div>
            <div class="counter-text">
              Months
            </div>
          </div>
          <div v-if="date.days" class="counter-item">
            <div class="counter-item-date">
              {{ date.days }}
            </div>
            <div class="counter-text">
              Day
            </div>
          </div>
          <div v-if="date.hours" class="counter-item">
            <div class="counter-item-date">
              {{ date.hours }}
            </div>
            <div class="counter-text">
              Hour
            </div>
          </div>
          <div v-if="date.minutes" class="counter-item">
            <div class="counter-item-date">
              {{ date.minutes }}
            </div>
            <div class="counter-text">
              Min
            </div>
          </div>
          <div v-if="date.seconds" class="counter-item">
            <div class="counter-item-date">
              {{ date.seconds }}
            </div>
            <div class="counter-text">
              Sec
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import firebase from "firebase";
import { db } from "../main";
export default {
  name: "ShowRun",
  props: ["run"],
  data: function() {
    return {
      date: "",
      timeToRun: "",
    };
  },
  mounted: function() {
    this.$nextTick(function() {
      window.setInterval(() => {
        this.createTimer(this.run.endDateOfRun);
      }, 1000);
    });
  },

  methods: {
    createTimer(endDate) {
      var now = moment().format("DD/MM/YYYY HH:mm:ss");
      var then = endDate;
      var ms = moment(then, "DD/MM/YYYY HH:mm:ss").diff(
        moment(now, "DD/MM/YYYY HH:mm:ss")
      );
      var d = moment.duration(ms);
      if (d < 0) {
        this.timeToRun = "Time to run, good luck !";
      } else {
        const date = {
          years: d.years(),
          months: d.months(),
          days: d.days(),
          hours: d.hours(),
          minutes: d.minutes(),
          seconds: d.seconds(),
        };
        this.date = date;
      }
    },
    deleteRun(run) {
      const uid = firebase.auth().currentUser.uid;
      var vm = this;
      db.collection("runs")
        .doc(uid)
        .collection("myruns")
        .doc(run.id)
        .delete()
        .then(function() {
          vm.$parent.fetchRuns();
        })
        .catch(function(error) {
          console.error("Error removing document: ", error);
        });
    },
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Staatliches&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Yellowtail&display=swap");

.icon {
  margin-right: 10px;
  color: #52527a;
}

.icon-stopwatch {
  margin-left: 10px;
  font-size: 40px;
  color: #400080;
}

.showrun-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-radius: 5px 5px 5px 5px;
  -moz-border-radius: 5px 5px 5px 5px;
  -webkit-border-radius: 5px 5px 5px 5px;
  border: 2px solid #9933ff;
  background-color: #fff;
  margin-top: 40px;
  color: black;
}
.column-left {
  display: grid;
  align-items: center;
  font-family: "Lobster Two", cursive;
}
.column-right {
  display: grid;
}
.inner-up {
  align-items: center;
  color: black;
  font-size: 25px;
}

.inner-down {
  font-size: 20px;
}

.run-name {
  margin-bottom: 5px;
  border-bottom: 3px solid #9933ff;
}

.inner-wrapper {
  display: grid;
  grid-template-rows: 1fr 1fr;
  padding: 10px;
}
.counter {
  display: flex;
  background-color: #fff;
  border: 3px solid #9933ff;
  margin: 20px;
  border-radius: 8px 8px 8px 8px;
  -moz-border-radius: 8px 8px 8px 8px;
  -webkit-border-radius: 8px 8px 8px 8px;
  color: black;
  font-family: "Staatliches", cursive;
  align-items: center;
  justify-content: center;
}
.counter-item {
  padding-right: 15px;
  padding-left: 15px;
  border-right: 2px solid black;
  align-items: center;
}

.counter-text {
  font-size: 35px;
  color: black;
}
.counter-item-date {
  font-size: 35px;
}
.icon-trash {
  font-size: 20px;
  margin: 25px;
  color: #e60000;
  cursor: pointer;
}

@media screen and (max-width: 800px) {
  .counter {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 15px;
  }
  .counter-item {
    margin: 2px;
    border-right: 0px solid black;
    border-bottom: 1px solid black;
  }

  .inner-up {
    align-items: center;
    color: black;
    font-size: 20px;
  }

  .inner-down {
    font-size: 15px;
  }

  .icon {
    margin-right: 10px;
    color: #52527a;
  }

  .icon-stopwatch {
    margin-right: 20px;
    font-size: 0px;
    color: #400080;
  }

  .counter-item-date {
    font-size: 25px;
  }

  .counter-text {
    font-size: 30px;
  }

  .counter {
    border: 2px solid #9933ff;
    padding: 15px;
  }
  .inner-up {
    font-size: 18px;
  }

  .run-name {
    font-size: 14px;
  }
  .icon-trash {
    font-size: 15px;
    margin: 25px;
    color: #e60000;
    cursor: pointer;
  }
}
</style>
