<template>
  <div class="main-container">
    <div class="nav-wrapper">
      <div class="logo">Runnter</div>
      <ul id="navigation">
        <li v-if="!user">
          <a><router-link to="/login">LOGIN</router-link></a>
        </li>
        <li v-if="!user">
          <a><router-link to="/signup">SIGNUP</router-link></a>
        </li>
        <li v-if="user">
          <a><router-link to="/runs">MY RUNS</router-link></a>
        </li>
        <li v-if="user">
          <a href="#" @click="logout">LOGOUT</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
export default {
  name: "Navbar",
  data() {
    return {
      user: null,
    };
  },
  created: function() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.user = user;
      } else {
        this.user = null;
      }
    });
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace("login");
        });
    },
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;800&display=swap");
a {
  text-decoration: none;
  color: grey;
  font-size: 16px;
}

#navigation {
  margin: 0;
}

.logo {
  margin-left: 50px;
  font-weight: 800;
  color: black;
  font-size: 30px;
}

#navigation li:hover a {
  color: #fff;
}

#navigation li:hover {
  border-radius: 5px 5px 5px 5px;
  background-color: #9933ff;
}

#navigation li {
  display: inline-block;
  margin-right: 5px;
  margin-left: 10px;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 5px;
  padding-top: 5px;
}

.nav-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "Nunito", sans-serif;
}
</style>
