<template>
  <div class="main-container">
    <div class="addRun-wrapper">
      <form class="addRun-form">
        <div>
          <label>Name :</label>
          <input class="input-field" type="text" v-model="name" name="name" />
        </div>
        <div>
          <label>Location :</label>
          <input
            class="input-field"
            v-model="where"
            type="text"
            name="location"
          />
        </div>
        <div>
          <label>Date :</label>
          <input class="input-field" v-model="date" type="date" name="date" required/>
        </div>
        <div>
          <label>Time :</label>
          <input v-model="time" type="time" required />
        </div>
        <div>
          <label>Distance :</label>
          <input style="max-width: 80px; margin-right: 5px;"
            class="input-field"
            type="number"
            v-model="distance"
            name="distance"
            min="1"
          /> km
        </div>
      </form>
      <div><button @click="addNewRun">Add Run</button></div>
    </div>
    <div class="error-message">{{error}}</div>
  </div>
</template>

<script>
import firebase from "firebase";
import { db } from "../main";
export default {
  name: "AddRun",
  data: function() {
    return {
      name: "",
      where: "",
      date: "",
      time: "",
      distance: "",
      error: ""
    };
  },
  methods: {
    addNewRun: function() {
      const dateSplit = this.date.split("-");
      const year = dateSplit[0];
      const month = dateSplit[1];
      const day = dateSplit[2];

      if(!this.name || !this.where || !this.date || !this.time || !this.distance){
        this.error = "Please complete the form"
        return;
      }


      const newRun = {
        name: this.name,
        where: this.where,
        endDateOfRun: `${day}/${month}/${year} ${this.time}`,
        distance: this.distance,
      };

      const uid = firebase.auth().currentUser.uid;
      db.collection("runs")
        .doc(uid)
        .collection("myruns")
        .add(newRun)
        .then(() => {
          console.log("New run have been created");
          this.$parent.fetchRuns();
          this.error = '';
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&display=swap");
.addRun-form {
  display: grid;
  font-size: 25px;
}
.addRun-wrapper {
  display: inline-block;
  font-family: "Lobster Two", cursive;
  color: black;
}

.addRun-form > * {
  margin: 15px;
}

.addRun-form label {
  margin-right: 20px;
}

.addRun-form input {
  font-size: 25px;
  border-radius: 5px 5px 5px 5px;
  font-family: "Lobster Two", cursive;
  border: 1px solid #400080;
  text-align: center;
}

button {
  border: 1px solid #400080;
  font-size: 25px;
  border-radius: 5px 5px 5px 5px;
  font-family: "Lobster Two", cursive;
  background-color: #400080;
  color: white;
}
.error-message {
  margin-top:30px;
  font-size: 25px;
  font-family: "Lobster Two", cursive;
  color: #e60000;
}


</style>
