<template>
  <div class="main-container">
    <div class="runs-wrapper">
      <div class="addRun-wrapper">
        <AddRun />
      </div>
      <div v-if="runs.length !=0" class="list-wrapper">
        <ShowRun
          v-for="run in runs"
          v-bind:key="run.id"
          v-bind:run="run"
        ></ShowRun>
      </div>
      <div v-else>
        <p class="runs-info">You don't have any run here</p>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import { db } from "../main";
import AddRun from "../components/AddRun";
import ShowRun from "../components/ShowRun";

export default {
  name: "Runs",
  components: {
    AddRun,
    ShowRun,
  },

  data() {
    return {
      runs: [],
    };
  },

  mounted() {
    this.fetchRuns();
  },

  methods: {
    fetchRuns() {
      const uid = firebase.auth().currentUser.uid;
      let runsArray = [];
      db.collection("runs")
        .doc(uid)
        .collection("myruns")
        .orderBy("endDateOfRun", "asc")
        .get()
        .then(function(querySnapshot) {
          querySnapshot.forEach(function(doc) {
            const run = {
              id: doc.id,
              ...doc.data(),
            };

            runsArray.push(run);
          });
        });
      this.runs = runsArray;
    },
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&display=swap");
.runs-wrapper {
  display: grid;
  grid-template-rows: auto 1fr;
}
.runs-info{
  font-size: 45px;
  color: #400080;
  font-family: "Lobster Two", cursive;
  border-radius: 5px 5px 5px 5px;
  border: 4px solid #400080;
}
</style>
