import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import firebase from "firebase";
import VueFirestore from 'vue-firestore'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faRoad, faMapMarkerAlt, faCalendarAlt, faStopwatch, faRunning, faTrash} from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { faFontAwesome, faGooglePlus } from '@fortawesome/free-brands-svg-icons'

library.add(faRoad, faMapMarkerAlt, faCalendarAlt, faStopwatch, faRunning, faTrash )

Vue.component('font-awesome-icon', FontAwesomeIcon)
library.add(faFontAwesome, faGooglePlus)

require('firebase/firestore')
Vue.use(VueFirestore)
Vue.use(require('vue-moment'));

Vue.config.productionTip = false;

let app = '';

const firebaseConfig = {
  apiKey: process.env.VUE_APP_API_KEY,
  authDomain: process.env.VUE_APP_AUTH_DOMAIN,
  databaseURL: process.env.VUE_APP_DATABASE_URL,
  projectId: process.env.VUE_APP_PROJECT_ID,
  storageBucket: process.env.VUE_APP_STORAGE_BUCKET,
  messagingSenderId: process.env.VUE_APP_MESSAGING_SENDER_ID,
  appId: process.env.VUE_APP_APP_ID,
  measurementId: process.env.VUE_APP_MEASUREMENT_ID,
};
// Initialize Firebase
firebase.initializeApp(firebaseConfig);
firebase.analytics();
export const db = firebase.firestore();

firebase.auth().onAuthStateChanged(() => {
  if (!app) {
    /* eslint-disable no-new */
    app = new Vue({
      router,
      render: h => h(App)
    }).$mount('#app');
  }
});
